<style lang="scss">
	uni-page-head{
	        display: block;
	    }
	/*每个页面公共css */
	/* 头部标题的样式 */
	uni-page-head .uni-page-head__title {
		font-family: 'PingFang_Regular' !important;
		color: #333333 !important;
		font-size: 32rpx !important;
	}

	uni-page-head .uni-page-head .uni-page-head-ft {
		padding-right: 60rpx;
	}
	/* 弹框的样式 */
	uni-modal .uni-modal {
	    position: fixed;
	    z-index: 999;
	    width: 80%;
	    max-width: 300px;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%,-50%);
	    transform: translate(-50%,-50%);
	    background-color: #fff;
	    text-align: center;
	    border-radius: 20px;
	    overflow: hidden;
	}
	// panel
	.tj-panel {
	  padding: 15px;
	
	  &.no-padding {
	    padding: 0;
	  }
	
	  &.is-round {
	    border-radius: 5px;
	  }
	
	  &.is-shadow {
	    box-shadow: 0px 6px 7px 0px #eee;
	  }
	
	  &.is-gutter {
	    margin-left: 15px;
	    margin-right: 15px;
	  }
	
	  &.tj-panel--dark {
	    color: #333;
	    background: #eee;
	  }
	
	  &.tj-panel--light {
	    background: #fff;
	  }
	
	  &.tj-panel--plain {
	    border: 1px solid #eee;
	    background: #fff;
	  }
	}
	// layout
	.tj-row {
	  width: 100%;
	  overflow: hidden;
	
	  .tj-row__inner {
	    &:before {
	      content: '';
	      display: table;
	    }
	
	    &::after {
	      content: '';
	      display: table;
	      clear: both;
	    }
	  }
	}
	
	.tj-col {
	  box-sizing: border-box;
	  line-height: 19px;
	  &.is-vertical:after {
	    content: '';
	    width: 0;
	    height: 100%;
	    display: inline-block;
	    vertical-align: middle;
	  }
	
	  &.tj-col-2 {
	    width: 16.66%;
	    float: left;
	  }
	
	  &.tj-col-3 {
	    width: 25%;
	    float: left;
	  }
	
	  &.tj-col-4 {
	    width: 33.33%;
	    float: left;
	  }
	
	  &.tj-col-5 {
	    width: 41.66%;
	    float: left;
	  }
	
	  &.tj-col-6 {
	    width: 50%;
	    float: left;
	  }
	
	  &.tj-col-7 {
	    width: 58.33%;
	    float: left;
	  }
	
	  &.tj-col-8 {
	    width: 66.66%;
	    float: left;
	  }
	}
	// cell
	.tj-cell {
	  .tj-cell__inner {
	    background: #fff;
	    color: #333;
	    display: flex;
	    height: 45px;
	    line-height: 45px;
	    align-items: center;
	
	    .tj-cell__icon {
	      flex: 0 0 20px;
	      margin-left: 10px;
	    }
	
	    .tj-cell__title {
	      flex: 1;
	    }
	
	    .tj-cell__value {
	      white-space: nowrap;
	      text-overflow: ellipsis;
	      overflow: hidden;
	      width: 55%;
	      text-align: right;
	
	      &.is-gray {
	        color: #999;
	      }
	    }
	
	    .tj-cell__solt {
	      // padding-right 5px
	    }
	
	    .tj-cell__right {
	      flex: 0 0 20px;
	      color: #ccc;
	      text-align: right;
	    }
	  }
	}

	.tj-popup{
		background :#fff;
		position: fixed;
		width: 100%;
		z-index :1;
		transition: all 1s linear;
		&.is-top{
			left :0;
			top: 0;
		}
		
		&.move-enter-active, &.move-leave-active{
			transform: translate3d(0, 0, 0);
		}
		
			
		&.move-enter, &.move-leave-active{
			transform :translate3d(0, -100%, 0);
		}
		
		&.is-right{
			width: 70%;
			overflow-y :auto;
			height :100%;
			right: 0;
			top :0;
		}
		
		&.move-enter-active, &.move-leave-active{
			transform: translate3d(0, 0, 0);
		}
		
		&.move-enter, &.move-leave-active{
			transform :translate3d(100%, 0, 0);
		}
		
		&.is-bottom{
			left :0;	
			bottom: 0;
		}
		
		&.move-enter-active, &.move-leave-active{
			transform: translate3d(0, 0, 0);
		}
		
		&.move-enter, &.move-leave-active{
			transform: translate3d(0, 100%, 0);
		}
		
		.tj-popup__main {
		max-height :300px;
		overflow-y: auto;
		}
		&.is-left{
			left: 0;
			top :0;
		}
		
		&.move-enter-active, &.move-leave-active{
			transform :translate3d(0, 0, 0);
		}
		
		&.move-enter, &.move-leave-active{
			transform :translate3d(-100%, 0, 0);
		}
		
		.tj-popup__head{
			display: flex;
			align-items :center;
			padding: 15px 15px 0;
			// border-bottom 1px solid $color-light-grey-ss
		}
		
			
		.tj-popup__title{
			flex: 1;
			font-weight: bolder;
			color: #333;
			font-size :16px;
		}
		
			
		.tj-popup__btn{
			flex :1;
			text-align: right;
		}
		
			
		.tj-popup__main{
			padding: 15px;
		}
		
	}
	
</style>
